@import "../../styles/media-queries";
@import "../../styles/font-size";
@import "../../styles/variables";

.hourly {
  @include mobile-small-portrait {
    grid-column: span 4;
    grid-row: 5;
  }
  @include mobile-small-landscape {
    grid-column: span 4;
    grid-row: 4;
  }
  @include mobile-portrait {
    grid-column: span 4;
    grid-row: 5;
  }
  @include mobile-landscape {
    grid-column: span 4;
    grid-row: 4;
  }
  @include tablet-portrait {
    grid-column: span 4;
    grid-row: 5;
  }
  @include tablet-landscape {
    grid-column: span 4;
    grid-row: 4;
  }
  @include desktop {
    grid-column: span 4;
    grid-row: 4;
  }
  display: flex;
  flex-direction: column;
  
  .title {
    color: var(--text);
    font-family: "Frank Ruhl Libre", serif;
    font-size: $headline4-font-size;
    font-weight: 700;
    margin-bottom: -5px;
  }

  .hourly-items-container {
    white-space: nowrap;
    scroll-snap-type: proximity;
    -webkit-tap-highlight-color: transparent;
    cursor: pointer;
    .hourly-item-container {
      &.active {
        
          background-color: var(--background-card);
          color: var(--text);
          box-shadow: 0px 5px 10px 0px var(--color-shadow);
          font-weight: 600;
      }
        background-color: var(--background-unselected-card);
        color: var(--text-secondary);
        box-shadow: 0;
        font-weight: 400;
      display: inline-block;
      height: 120px;
      width: 75px;
      margin: 15px 5px;
      border-radius: 15px;
      transition: all $animation-time ease;
    }
  }
}